import Image from "next/image";
import logo_text from "../../public/Logos/Logo_Text_svg.svg";
import { Button } from "@/components/ui/button";
import { NavBar } from "./navbar";

export default function Header() {
  return (
    <div className="w-full h-[5vw] content-center  backdrop-blur-md backdrop-brightness-150 sticky top-0">
      <div className="container flex justify-between">
        <Image
          alt="logo"
          src={logo_text}
          width={700}
          height={700}
          className="w-[10vw] h-auto"
        />
        <div>
          <NavBar />
        </div>
        <Button variant="default" className="bg-main rounded-full">
          Book a demo
        </Button>
      </div>
    </div>
  );
}
